.contacts-page {
	max-width: 900px;
	margin: 40px auto 0 auto;
	padding: 40px 32px 32px 32px;
	border: 1px solid #ffffff;
}

.contacts-title {
	text-align: center;
	font-size: 32px;
	font-weight: 400;
	margin-bottom: 32px;
	letter-spacing: 1px;
}

.contacts-block {
	display: flex;
	gap: 24px;
	margin-bottom: 24px;
}

.contacts-info {
	flex: 1;
	border: 1px solid #e0e0e0;
	padding: 24px;
	font-size: 16px;
	display: flex;
	flex-direction: column;
	gap: 10px;
	background-color: rgba(0, 0, 0, 0.621);
}

.contacts-row {
	display: flex;
	justify-content: space-between;
	margin-bottom: 8px;
}

.contacts-info hr {
	margin: 12px 0;
	border: none;
	border-top: 1px solid #aaa;
}

.contacts-info a {
	color: #ffffff;
	text-decoration: underline;
	word-break: break-all;
}

.contacts-map {
	flex: 1;
	min-width: 320px;
	min-height: 260px;
	border: 1px solid #aaa;
	background: #eee;
}

.contacts-feedback {
	width: 100%;
	min-height: 80px;
	margin: 16px 0;
	padding: 12px;
	font-size: 16px;
	border: 1px solid #aaa;
	resize: vertical;
	box-sizing: border-box;
}

.contacts-submit {
	display: block;
	margin-left: auto;
	padding: 8px 32px;
	font-size: 16px;
	border: 1px solid #222;
	background: #f8f8f8;
	cursor: pointer;
	transition: background 0.2s;
}

.contacts-submit:hover {
	background: #e0e0e0;
}

@media (max-width: 768px) {
	.contacts-page {
		max-width: 100%;
		margin: 20px 10px 0 10px;
		padding: 20px 15px;
		border: none;
	}
	.contacts-title {
		font-size: 28px;
		margin-bottom: 24px;
	}

	.contacts-block {
		flex-direction: column;
		gap: 20px;
		margin-bottom: 20px;
	}

	.contacts-info,
	.contacts-map {
		flex: none;
		width: 100%;
		min-width: auto;
		min-height: 200px;
		padding: 20px;
		border: 1px solid #ccc;
		background-color: rgba(0, 0, 0, 0.6);
	}

	.contacts-info {
		font-size: 15px;
	}

	.contacts-row {
		font-size: 15px;
		margin-bottom: 6px;
	}

	.contacts-info hr {
		margin: 10px 0;
	}

	.contacts-info a {
		font-size: 15px;
	}

	.contacts-feedback {
		font-size: 15px;
		min-height: 100px;
		padding: 10px;
	}

	.contacts-submit {
		width: 100%;
		padding: 12px 0;
		font-size: 16px;
		margin-left: 0;
	}
}
@media (max-width: 480px) {
	.contacts-title {
		font-size: 24px;
		margin-bottom: 20px;
	}

	.contacts-info,
	.contacts-map {
		padding: 15px;
		font-size: 14px;
	}

	.contacts-row {
		font-size: 14px;
	}

	.contacts-info a {
		font-size: 14px;
	}

	.contacts-feedback {
		font-size: 14px;
		min-height: 120px;
		padding: 10px;
	}

	.contacts-submit {
		font-size: 14px;
		padding: 10px 0;
	}
}
