.menu-container {
	max-width: 980px;
	margin: 0 auto;
	padding: 20px;
}

.menu-title {
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	margin-bottom: 40px;
	color: #fff;
}

.menu-section-title {
	font-size: 28px;
	margin-top: 40px;
	margin-bottom: 20px;
	color: #fff;
}

.menu-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.6);
	padding: 15px 20px;
	margin-bottom: 20px;
	border-radius: 8px;
	color: #fff;
}

.menu-item-info {
	max-width: 60%;
}

.menu-item-title {
	font-size: 22px;
	font-weight: 600;
	margin-bottom: 8px;
}

.menu-item-desc {
	font-size: 16px;
	margin-bottom: 8px;
	line-height: 1.4;
}

.menu-item-price {
	font-size: 18px;
	font-weight: 700;
	color: #f0c040;
}

.menu-item-img {
	max-width: 150px;
	height: auto;
	border-radius: 8px;
	object-fit: cover;
	margin-left: 20px;
}

/* Адаптивность для экранов до 768px */
@media (max-width: 768px) {
	.menu-item {
		flex-direction: column;
		align-items: flex-start;
	}

	.menu-item-info {
		max-width: 100%;
	}

	.menu-item-img {
		max-width: 100%;
		margin: 15px 0 0 0;
	}

	.menu-title {
		font-size: 28px;
	}

	.menu-section-title {
		font-size: 22px;
	}

	.menu-item-title {
		font-size: 18px;
	}

	.menu-item-desc,
	.menu-item-price {
		font-size: 14px;
	}
}

/* Адаптивность для экранов до 480px */
@media (max-width: 480px) {
	.menu-container {
		padding: 10px;
	}

	.menu-title {
		font-size: 24px;
	}

	.menu-section-title {
		font-size: 18px;
	}

	.menu-item-title {
		font-size: 16px;
	}

	.menu-item-desc,
	.menu-item-price {
		font-size: 12px;
	}
}
